import React from 'react'
import _ from 'lodash'
import styled from 'styled-components'
import ContentLoader from 'react-content-loader'
import { Alert, Button } from 'reactstrap'
import Card from './Card'
import { colors } from '../../../constants'

const Container = styled.div`
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  @media (min-width: 576px) {
    margin-top: 80px;
  }
`
const EOLAlert = () => (
  <Alert color="dark" style={{ textAlign: 'center' }}>
    That's all for now!
  </Alert>
)

const Loader = () => (
  <React.Fragment>
    {
      Array.from({ length: 10 }).map((v, i) => {
        return (
          <ContentLoader
            key={i}
            ariaLabel="Loading..."
            width="100%"
            style={{
              marginBottom: '12px',
              marginLeft: '8px',
              height: '100px',
              width: '100%',
              borderRadius: '4px',
              boxShadow: `0 0 2px ${colors.gray300}`
            }}
          >
            <rect x="0" y="0" height="100%" width="100" />
            <rect x="110" y="10" rx="2" ry="6" width="200" height="28" />
            <rect x="110" y="52" rx="2" ry="6" height="16" style={{ width: "calc(100% - 130px)" }} />
            <rect x="110" y="76" rx="2" ry="6" height="16" style={{ width: "calc(100% - 150px)" }} />
          </ContentLoader>
        )
      })
    }
  </React.Fragment>
)

class Content extends React.Component {
  state = {
    page: 0
  }
  componentDidUpdate(prevProps) {
    if (prevProps.sourceSite !== this.props.sourceSite) {
      this.setState({ page: 0 })
    }
  }
  loadMoreItems = async () => {
    const { page } = this.state
    const nextPage = page + 1
    await this.props.loadMore(nextPage)
    this.setState({ page: nextPage })
  }
  insertItemDates = (items) => {
    return _.reduce(items, (acc, item, i) => {
      if (item.type === 'date' || !item.sourcePublishedAt) {
        return acc
      }
      const ts = new Date(item.sourcePublishedAt)
      const monthNames = ["January", "February", "March", "April", "May", "June",
        "July", "August", "September", "October", "November", "December"
      ]
      const date = `${monthNames[ts.getMonth()]} ${ts.getDate()}`
      const foundDateItem = _.find(items, { type: 'date', date })
      if (!foundDateItem) {
        acc.splice(i, 0, {
          type: 'date',
          date,
          firstItemId: item.id
        })
      }
      return acc
    }, items)
  }
  renderList = () => {
    const { items, sourceSite, loading } = this.props
    const itemsWithDates = this.insertItemDates(items)
    if (loading && items.length === 0) {
      return <Loader />
    }
    return itemsWithDates.map((data, key) => {
      if (data.type === 'date') {
        return (
          <h3 style={{ margin: '12px', paddingBottom: '6px', textAlign: 'right', color: colors.gray500, borderBottom: `1px solid ${colors.gray300}` }} key={key}>
            {data.date}
          </h3>
        )
      }
      return (
        <Card {...{ data, key, sourceSite }} />
      )
    })
  }
  renderFooter = () => {
    const { items, totalItems, loading } = this.props
    if (
      _.isArray(items) &&
      _.filter(items, item => item.type !== 'date').length === totalItems
    ) {
      return <EOLAlert />
    }
    return (
      <Button
        onClick={this.loadMoreItems}
        disabled={loading}
        style={{ marginBottom: '20px' }}
      >
        {loading ? 'Loading...' : 'Load More'}
      </Button>
    )
  }
  render() {
    return (
      <Container>
        {this.renderList()}
        {this.renderFooter()}
      </Container>
    )
  }
}

export default Content
